import { MainService } from '../../services/main.service';
import { Utils } from '../../services/utils.service';
import { Component, OnInit } from '@angular/core';

@Component({
    selector: 'app-profile-redirect',
    template: ''
})
export class ProfileRedirectComponent implements OnInit {

    constructor(public main: MainService) { }

    async ngOnInit() {
        try {
            // let username = (await this.main.provider.getCurrentUser())["username"];
            let username = this.main.currentUser.username;
            if (username) this.main.goTo(username);
            else new Error();
        } catch {
            this.main.goTo("/login");
        }
    }
    ngOnDestroy() { this.main.onDestroyPage(); }
}
